import { json, redirect } from "@remix-run/node";
import { useLoaderData, useCatch, Link } from "@remix-run/react";
import connectDb from "~/db/connectDb.server.js";

export async function loader({ params }) {
  const db = await connectDb();

  const post = await db.models.Post.findById(params.postId);
  if (!post) {
    throw new Response(
      `I don't seem to be able to find a post with that id: ${params.postId} ðŸ¤”ðŸ”Ž `,
      {
        status: 404,
      }
    );
  }
  return json(post);
}

//DELETE SELECTED CODE POST

export const action = async ({ request, params }) => {
  const db = await connectDb();
  const form = await request.formData();
  if (form.get("_method") === "delete") {
    const post = await db.models.Post.findById(params.postId);
    if (!post)
      throw new Error("The post you are trying to delete doesn't exist");
    await post.delete({ where: { id: params.postId } });
    return redirect("../");

    //ADD SELECTED CODE POST TO FAVORITE / REMOVE FROM FAVORITE
  } else if (form.get("_method") === "favorite") {
    const post = await db.models.Post.findById(params.postId);
    post.favorite = !post.favorite;
    await post.save();
  }
  return null;
};

export default function PostPage() {
  const post = useLoaderData();
  const dateTime = new Date(post.createdAt);
  const date = dateTime.getDate();
  const month = dateTime.getMonth() + 1;
  const timeCreated = dateTime.toLocaleTimeString();
  return (
    <div>
      <div>
        <div>
          <Link to="../">Back to all posts</Link>

          <h1 className="text-2xl font-bold">
            {post.title}
            <div className="float-right">
              <form method="POST">
                <input type="hidden" name="_method" value="favorite" />
                <button type="submit">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    className="h-6 w-6"
                    fill={post.favorite ? "red" : "none"}
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    strokeWidth={2}
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
                    />
                  </svg>
                </button>
              </form>
            </div>
            <span className="text-xl font-normal"> - {post.language}</span>
          </h1>
        </div>
        <p>Added {`${date}/${month}/${timeCreated}`}</p>
        <p className="italic">- {post.description}</p>
        <pre className="text-blue-600 rounded-2xl bg-gray-800 p-6 mt-4 whitespace-normal">
          {post.post}
        </pre>
      </div>

      {/* <code>
        <pre>{JSON.stringify(post, null, 2)}</pre>
      </code> */}

      <div className="flex justify-between items-center mt-6">
        <form method="POST">
          <input type="hidden" name="_method" value="delete" />
          <button className="shadow-lg bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 rounded">
            Delete post
          </button>
        </form>

        <div className="float-right">
          <form method="POST">
            <input type="hidden" name="_method" value="update" />
            <Link
              className="hover:underline"
              to={`/posts/update/${post._id}`}
            >
              <button className="shadow-lg bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 rounded">
                Update post
              </button>
            </Link>
          </form>
        </div>
      </div>
    </div>
  );
}

export function CatchBoundary() {
  const caught = useCatch();
  return (
    <div>
      <h1>
        {caught.status} {caught.statusText}
      </h1>
      <h2>{caught.data}</h2>
    </div>
  );
}

export function ErrorBoundary({ error }) {
  return (
    <h1 className="text-red-500 font-bold">
      {error.name}: {error.message}
    </h1>
  );
}
